.setup-page {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
    justify-items: center;
    padding: 20px;

    .container {
        display: flex;
        flex-direction: column;
        justify-items: center;
        align-items: center;

        .card {
            display: flex;
            flex-direction: column;
            margin: 8px;
            box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.5);
            padding: 20px;

            h3 {
                margin-top: 5px;
                margin-bottom: 5px;
            }
        }

        .row {
            display: flex;
            min-width: 320px;
            flex-direction: row;
            margin: 4px;
            padding: 8px;
            justify-content: space-between;
            box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.5);
        }
    }
}

.trackable-text-input {
    width: 100%;
    padding: 4px;
    font-size: 16px;
}

.trackable-submit {
    padding: 10px;
    font-size: 16px;
    background-color: rgb(111, 50, 172);
    color: White;
    border: none;
    margin: 10px;
    margin-left: 0;
    cursor: pointer;

    &:hover {
        background-color: rgb(178, 85, 201);
        transition: 0.1s ease-out;
    }

    &:active {
        background-color: rgb(78, 36, 133);
    }
}
