.navbar {
    background-color: rgb(113, 59, 199);
    background: linear-gradient(
        to bottom,
        rgb(199, 120, 255),
        rgb(113, 59, 199),
        rgb(113, 59, 199),
        rgb(113, 59, 199),
        rgb(84, 38, 148)
    );
    color: white;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 10px;

    h1 {
        color: white;
        font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
        font-weight: lighter;
        margin: 0;
        padding: 8px 10px;
        line-height: 100%;
    }
}
