.check-in--form {
    margin: 32px;
    padding: 16px;
    box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.5);
    height: fit-content;

    display: flex;
    flex-direction: column;

    font-weight: lighter;
    text-align: center;

    h2,
    h4 {
        margin: 4px;
    }

    h4 {
        border-bottom: 1px solid gray;
        padding-bottom: 8px;
    }

    .check-card {
        padding: 4px;
        margin-top: 8px;
        display: flex;
        flex-direction: column;
        text-align: center;
    }

    .check-in-note-input {
        padding: 3px;
        font-size: 16px;
        overflow-y: visible;
    }

    .check-in-submit {
        padding: 16px;
        font-size: 16px;
        background-color: rebeccapurple;
        color: white;
        border: none;
        border-radius: 4px;
        width: fit-content;
        margin: auto auto;
        cursor: pointer;
        transition: 0.1s ease-out;

        &:hover {
            background-color: rgb(150, 69, 177);
        }

        &:active {
            background-color: rgb(68, 32, 116);
        }
    }
}
