.heat-selector {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    margin: 8px;
    border: 1px solid black;

    .heat-block {
        border: 1px solid black;
        font-size: 18px;
        line-height: 48px;
        width: 48px;
        height: 48px;

        &:hover {
            background-color: cyan;
            cursor: pointer;
        }

        &.selected {
            background-color: yellow;
        }
    }
}
